.title {
  font-weight: 100; }

select {
  font-size: 12pt;
  width: 100%;
  padding: 15px 0 15px 20px;
  border: 0 !important;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='100' height='100'><polygon points='0,0 100,0 50,50'/></svg>") no-repeat;
  background-size: 10px;
  border-radius: 10px;
  background-position: calc(100% - 20px) calc(50% + 2.5px);
  background-repeat: no-repeat;
  background-color: #e9e9e9; }

#search {
  font-size: 14pt; }

.coupons-results {
  margin-top: 20px;
  overflow: scroll; }

.loading-container {
  height: 500px; }

.coupons-results-table thead th {
  width: 200px; }

.coupons-results-table td {
  border-top: 2px solid #e9e9e9;
  border-bottom: 2px solid #e9e9e9;
  padding: 20px; }

@media (max-width: 600px) {
  .coupon-card {
    width: 100%; } }
