.dropdown {
  overflow: hidden;
  float: left;
  margin-left: 40px; }

.dropdown .dropbtn {
  border: none;
  outline: none;
  font-family: inherit;
  /* Important for vertical align on mobile phones */
  margin: 0;
  /* Important for vertical align on mobile phones */ }

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #fdfdfd;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  border-radius: 5px; }

/* Links inside the dropdown */
.dropdown-content a {
  text-align: center;
  padding: 12px 16px;
  text-decoration: none; }

.dropdown:hover .dropdown-content {
  display: block; }
